/*
*carouse1
*/
.carousel1 {
    display: flex;
    flex-wrap: wrap;
    .figure1 {
        img {
            border-radius: 10px;
        }
    }
    > .item {
        width: calc(16.666% - 24px);
        + .item {
            margin-left: 24px;
        }
    }
   > .item:nth-child(odd), .owl-stage .owl-item:nth-child(odd) .item {
        .figure1 {
            transform: rotate(5deg);
        }
    }
   > .item:nth-child(even), .owl-stage .owl-item:nth-child(even) .item {
        .figure1 {
            transform: rotate(-5deg);
        }
    }
    .owl-stage-outer {
        overflow: unset;
    }
}

/*
*carousel3
*/
.carousel2 {
    .owl-stage-outer {
        overflow: initial;
    }
    .owl-stage {
        display: flex;
        flex-wrap: wrap;
        .item {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px;
            img {
                max-height: 36px;
            }
        }
    }
}

/*
*carousel3
*/
.carousel3 {
    .owl-nav {
        position: absolute;
        width: 100%;
        display: flex;
        justify-content: space-between;
        top: 0;
        z-index: 200;
        button {
            span {
                display: none;
            }
            width: 106px;
            height: 110px;
        }
        .owl-prev {
            background: url('../images/left-arrow.svg') no-repeat !important;
            background-size: contain;
        }
        .owl-next {
            background: url('../images/right-arrow.svg') no-repeat !important;
            background-size: contain;
        }
    }
    .item {
        text-align: center;
        padding-top: 20px;
        img {
            width: auto;
            display: inline-block;
            filter: drop-shadow(10px 14px 20px rgba(103, 37, 103, 0.3));
        }
        .image {
            margin-bottom: -100px;
        }
        h4 {
            font-size: rem-calc(24);
            color: $white;
            line-height: 1;
            font-family: $font-family-secondary;
            font-feature-settings: "swsh" 1;
            margin-bottom: 2px;
            font-weight: 900;
        }
        .choose-me {
            font-family: $font-family-secondary;
            font-size: rem-calc(18);
            text-transform: uppercase;
        }
    }
    .owl-stage-outer {
        overflow: initial;
    }
    .owl-stage {
        display: flex;
        flex-wrap: wrap;
        .owl-item {
            .item {
                transition: all 300ms ease;
                transform: rotate(-20deg) translateY(400px) !important;
            }
            h4, span {
                opacity: 0;
               @include cubic-transition;
            }
            opacity: 0;
            @include cubic-transition;
        }
        .active {
            opacity: 1;
            .item {
                transform: rotate(-10deg) translateY(100px) !important;
            }
        }
        .center {
            .item {
                transform: rotate(0deg) translateY(0px) !important;
                h4, span {
                    opacity: 1;
                    
                }
            }
        }
        .center + .active {
            .item {
                transform: rotate(10deg) translateY(100px) !important;
            }
        }
        .active + .active + .active + .owl-item {
           .item {
            transform: rotate(20deg) translateY(400px) !important;
           }
        }
    }
}

@media (max-width: 1199px) {
    .carousel3 {
        .item {
            .image {
                img {
                    max-height: 240px;
                }
                margin-bottom: -66px;
            }
            h4 {
                font-size: rem-calc(20);
            }
        }
    }
}

@media (max-width: 991px) {
    .carousel3 {
        .item {
            .image {
                img {
                    max-height: 170px;
                }
                margin-bottom: -29px;
            }
            h4 {
                font-size: rem-calc(14);
            }
            .choose-me{
                font-size: rem-calc(12);
            }
        }
    }
}

@media (max-width: 767px) {
    .carousel3 {
        .owl-nav {
            button {
                transform: scale(0.3);
            }
        }
        .item {
            padding-top: 30px;
            .image {
                img {
                    max-height: 110px;
                }
                margin-bottom: -18px;
            }
            h4 {
                font-size: rem-calc(10);
            }
            .choose-me{
                font-size: rem-calc(8);
            }
        }
    }
}
