/*
*layout1
*/
.layout1 {
    #header {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
    #page-head  {
        padding: 319px 0 190px;
    }
}
 
@media (max-width: 991px) {
    .layout1 {
        #page-head {
            padding: 220px 0 138px;
        }
    }
}

@media (max-width: 991px) {
    .layout1 {
        #page-head {
            padding: 155px 0 96px;
        }
    }
}

/*
*layout2
*/
.layout2 {
    #header {
        position: relative;
        &:after {
            content: '';
            position: absolute;
            bottom: auto;
            top: 0;
            width: 100%;
            height: calc(100% + 65px);
            background: linear-gradient(to bottom, #a272af 0%, #a373b0 1%, rgba(247, 232, 243, 0.01) 99%, rgba(247, 232, 243, 0) 100%);
            z-index: -1;
        }
    }
} 