/*
*pagination
*/
.pagination {
    flex-wrap: wrap;
    .page-item {
        margin-right: 20px;
        font-size: rem-calc(20);
        line-height: 1;
        font-weight: 400;
        &:last-child {
            margin-right: 0;
        }
        &:first-child {
            margin-right: 54px;
        }
        &:last-child {
            margin-left: 34px;
        }
        a, span {
            border: 2px solid $primary;
            border-radius: 10px !important;
            @extend .bg-color1;
            box-shadow: 5px 7px 0 $primary;
            min-width: 42px;
            text-align: center;
            padding: 7px 5px;
            &.active, &:hover {
                color: $white;
                background: $primary !important;
                border-color: $primary-dark;
                box-shadow: 5px 7px 0 $primary-dark;
            }
            .or {
                pointer-events: none;
            }
        }
    }
}

@media (max-width: 991px) {
    .pagination {
        .page-item {
            &:first-child {
                margin-right: 30px;
            }
            &:last-child {
                margin-left: 10px;
            }
        }
    }
}

@media (max-width: 767px) {
    .pagination {
        .page-item {
            margin-right: 14px;
            &:first-child {
                margin-right: 30px;
            }
            &:last-child {
                margin-left: 16px;
            }
            a, span {
                font-size: rem-calc(14);
                min-width: 28px;
            }
        }
    }
}