/*
*list
*/
.list1 {
    padding: 0;
    list-style-type: none;
    margin-bottom: 24px;
    li {
        font-size: rem-calc(22);
        line-height: calc(28 / 22);
        color: #8D8D8D;
    }
}

@media (max-width: 1199px) {
    .list1  {
        margin-bottom: 20px;
        li {
            font-size: rem-calc(18);
        }
    }
}

/*
*list2
*/
.list2 {
    padding: 0;
    list-style-type: none;
    margin-bottom: 40px ;
    li {
        font-size: rem-calc(22);
        line-height: calc(30 / 22);
        color: $white;
        margin-bottom: 26px;
        position: relative;
        padding-left: 40px;
        &:before {
            content: '';
            width: 26px;
            height: 16px;
            background: url('../images/cloud-list.svg') no-repeat;
            position: absolute;
            top: 7px;
            left: 0;
        }
    }
}

@media (max-width: 767px) {
    .list2 {
        li {
            font-size: rem-calc(18);
        }
    }
}

/*
*desc-list
*/
.list3 {
    padding: 0;
    margin-left: 0;
    li {
        display: flex;
        flex-wrap: wrap;
        border-bottom: 2px dashed $primary-color4;
        padding: 24px 0 22px;
        span {
            width: 50%;
            color: $primary-color4;
            font-family: $font-family-secondary;
            font-weight: 400;
            line-height: 1.1;
            + span {
                padding-left: 15px;
            }
        }
        .value {
            text-align: right;
            font-weight: 700;
        }
    }
}

@media (max-width: 767px) {
    .list3 {
        li {
            font-size: rem-calc(16);
            padding: 18px 0 16px;
        }
    }
}

/*
*list4
*/
.list4 {
    padding: 0;
    list-style-type: none;
    li {
        display: flex;
        margin-bottom: 71px;
        &:last-child {
            margin-bottom: 0;
        }
        .image-control {
            flex-shrink: 0;
            width: 141px;
        }
        .content {
            padding-left: 40px;
            flex: 1;
            max-width: 295px;
            h4 {
                font-size: rem-calc(36);
                color: $primary-color4;
                font-family: $font-family-secondary;
                font-weight: 900;
                margin-bottom: 2px;
                transform: rotate(2deg);
                span {
                    font-size: rem-calc(24);
                    font-weight: 400;
                    display: block;
                    margin-bottom: 22px;
                    transform: rotate(-6deg);
                    margin-left: -63px;
                }
            }
            p {
                color: $white;
                font-size: rem-calc(18);
                line-height: calc(26 / 18);
            }
        }
    }
    li:nth-child(1) {
        padding-left: 14px;
    }
    li:nth-child(3) {
        padding-left: 90px;
    }
}

@media (max-width: 991px) {
    .list4 {
        li {
            margin-bottom: 30px;
            .content {
                padding-left: 15px;
                h4 {
                    font-size: rem-calc(24);
                    span {
                        font-size: rem-calc(18);
                    }
                }
                p {
                    font-size: rem-calc(14);
                }
            }
        }
        li:nth-child(3) {
            padding-left: 14px;
        }
    }
}

@media (max-width: 767px) {
    .list4 {
        li {
            .image-control {
                width: 95px;
            }
            .content {
                h4 {
                    font-size: rem-calc(22);
                    span {
                        margin-left: -40px;
                    }
                }
            }
        }
    }
}

/*
*list5
*/
.list5 {
    padding: 0;
    list-style-type: none;
    counter-reset: item;
    li {
        border: 2px solid $primary;
        background: $white;
        font-size: rem-calc(24);
        line-height: calc(28 / 24);
        box-shadow: 5px 7px 0 $primary;
        display: flex;
        align-items: center;
        border-radius: 10px;
        margin-bottom: 29px;
        padding: 7px 10px;
        &:before {
            content: counter(item);
            counter-increment: item;
            font-size: rem-calc(73);
            line-height: 1;
            font-weight: 700;
            flex-shrink: 0;
            min-width: 94px;
            text-align: center;
            padding: 0 10px 0 0;
        }
    }
}

@media (max-width: 767px) {
    .list5 {
        li {
            font-size: rem-calc(20);
            line-height: calc(24 / 20);
            &:before {
                min-width: 64px;
                font-size: rem-calc(50);
            }
        }
    }
}