/*
*post
*/
.post {
    .post-date {
        font-size: rem-calc(16);
        text-transform: uppercase;
        display: inline-block;
        margin-bottom: 10px;
    }
    figure {
        margin-bottom: 10px;
        img {
            border-radius: 20px;
            object-fit: cover;
        }
    }
    h2 {
        font-size: rem-calc(24);
        line-height: calc(32 / 24);
        margin-bottom: 10px;
    }
    p {
        font-size: rem-calc(20);
        line-height: calc(28 / 20);
        font-weight: 300;
        margin-bottom: 18px;
    }
}

.single-post {
    display: flex;
    flex-direction: column;
    height: 100%;
    .figure1 {
        img {
            height: 288px;
        }
    }
    footer {
        margin-top: auto;
    }
}

@media (max-width: 767px) {
    .post {
        h2 {
            font-size: rem-calc(20);
        }
        p {
            font-size: rem-calc(16);
            margin-bottom: 14px;
        }
    }
}