.relative {
    position: relative !important;
}

.heading1 {
    font-family: $font-family-tertiary;
    font-size: rem-calc(40);
    line-height: 1;
    font-weight: 400;
}

@media (max-width: 767px) {
    .heading1 {
        font-size: rem-calc(24);
    }
}

.row1 {
    margin-left: -1px;
    margin-right: -1px;
    > * {
        padding-left: 1px;
        padding-right: 1px;
    }
}

.overflow-x-initial {
    overflow-x: initial !important;
}

.shadow1 {
    filter: drop-shadow(10px 14px 20px rgba(103, 37, 103, 0.3)) !important;
}

.hide-wave-t {
    &:before {
        display: none;
    }
} 

.figure1 {
    img.w-100 {
        object-fit: cover;
    }
    &.cover-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        img.w-100 {
            height: 100%;
        }
    }
}

.pb-lg-6 {
    padding-bottom: 70px !important;
}

@media (min-width: 992px) {
    .pb-lg-7 {
        padding-bottom: 90px !important;
    }
}

@media (min-width: 992px) {
    .row2 {
        margin-left: -37px;
        margin-right: -37px;
        > div {
            padding-left: 37px;
            padding-right: 37px;
        }
    }
}