/*
*tabs1
*/
.tabs1 .tab-header {
    position: relative;
    font-size: rem-calc(18);
    border: 2px solid $primary;
    border-radius: 10px;
    padding: 10px 36px 10px 16px;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
}

.tabs1 .tab-header .title {
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
    font-weight: 700;
}

.tabs1 .tab-header .fa-bars {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
    color: #672e6c;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
}

.tabs1 .nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border: 0;
    border-radius: 10px;
    overflow: hidden;
}

.tabs1 .nav li button {
    display: block;
    width: 100%;
    font-size: rem-calc(40);
    font-family: $font-family-tertiary;
    color: $primary;
    line-height: 1;
    font-weight: 400;
    border: 0px;
    background: transparent;
    padding: 16px 18px;
    position: relative;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
    border-bottom: 1px solid $primary;
}

.tabs1 .nav li button:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    background: $primary;
    bottom: -2px;
    left: 0;
    opacity: 0;
    transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
}


.tabs1 .nav li button.active:after,
.tabs1 .nav li button:hover:after {
    opacity: 1;
}

.tabs1 .tab-content .tab-pane {
    padding: 35px 0 0;
}

.tabs1 .tab-content .tab-pane header {
    margin-bottom: 47px;
}

.tabs1 header p {
    font-size: rem-calc(22);
    font-weight: 300;
}

@media (max-width: 991px) {
    .tabs1 {
        .nav {
            li {
                button {
                    font-size: rem-calc(26);
                    padding: 6px 15px;
                }
            }
        }
    }
}

@media (max-width:767px) {
    .tabs1 {
        position: relative;
        padding-bottom: 1px;
    }

    .tabs1 .nav {
        display: block;
        position: absolute;
        width: 100%;
        top: 54px;
        left: 0;
        z-index: 200;
        opacity: 0;
        visibility: hidden;
        transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
        background: #fff;
        border: 2px solid $primary;
    }

    .tabs1 .nav li {
        width: 100%;
        text-align: left;
    }

    .tabs1 .nav li+li {
        margin-left: 0px;
    }

    .tabs1.open .tab-header {
        background: $primary;
        color: #fff;
    }

    .tabs1.open .tab-header .fa-bars {
        color: #fff;
    }

    .tabs1 .nav li button:after {
        display: none;
    }

    .tabs1 .nav li button.active,
    .tabs1 .nav li button:hover {
        background: $primary;
        color: $white;
    }

    .tabs1 .nav li button {
        border: 0;
    }

    .tabs1.open .tab-header .fa-bars:before {
        content: "\f00d";
    }

    .tabs1.open .nav {
        opacity: 1;
        visibility: visible;
    }

    .tabs1.alt .nav li {
        white-space: initial;
    }
    .tabs1 .tab-content .tab-pane header {
        margin-bottom: 14px;
    }
}

.tabs1 {
    &.alt {
        .tab-content {
            h5 {
                margin-bottom: 37px;
            }
            p {
                font-weight: 300;
                margin-bottom: 33px;
            }
        }
    }
}

@media (max-width: 767px) {
    .tabs1 {
        &.alt {
            .tab-content {
                h5 {
                    margin-bottom: 20px;
                }
                p {
                    font-size: rem-calc(18);
                    margin-bottom: 20px;
                }
            }
        }
    }
}

@media (min-width: 768px) {
    .tabs1 {
        .nav {
            overflow-x: auto;
            overflow-y:hidden;
            flex-wrap: nowrap;
            button {
                white-space: nowrap;
            }
        }
        &.alt {
            .nav {
                justify-content: space-between;
                li {
                    + li {
                        margin-left: 5px;
                    }
                    button {
                        font-size: rem-calc(42);
                        line-height: 1;
                        color: $primary-light;
                        padding: 0 3px;
                        border-bottom: 0;
                        &.active, &:hover {
                            color: $primary;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 991px) and (min-width: 768px) {
    .tabs1 {
        .nav {
            li {
                button {
                    font-size: rem-calc(30);
                }
            }
        }
    }
}
