/*
*bg color21
*/
.bg-color1 {
    background: #F7E8F3 !important;
}

.bg-color2 {
    background: rgb(247,232,243) !important;
    background: linear-gradient(0deg, rgba(247,232,243,1) 70%, rgba(213,183,217,1) 100%) !important;
}

.bg-color3 {
    background: #D5B7D9 !important;
}

.bg-gradient1 {
    background: -moz-linear-gradient(top,  rgba(247,232,243,0) 0%, rgba(162,114,175,1) 100%) !important;
    background: -webkit-linear-gradient(top,  rgba(247,232,243,0) 0%,rgba(162,114,175,1) 100%) !important;
    background: linear-gradient(to bottom,  rgba(247,232,243,0) 0%,rgba(162,114,175,1) 100%) !important; 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00f7e8f3', endColorstr='#a272af',GradientType=0 ) !important;
}

.peach-theme {
    background: #ffe2d1 !important;
    background: linear-gradient(0deg, #ffe2d1 70%, #edc4ad 100%) !important;
    .single-product-section .side-content {
        h1 {
            color: #BC7751;
        }
        p {
            color: #BC7751;
        }
        .flavours {
            h4 {
                color: #BC7751;
            }
            a {
                color: #BC7751;
                background: #EDC4AD;
                border-color: #BC7751;
                box-shadow: 5px 7px 0 #BC7751;
                &:hover {
                    background: #FCE3D1;
                }
            }
            .current {
                a {
                    background: #FCE3D1;
                }
            }
        }
        .product-price {
            li {
                .price {
                    color: #BC7751;
                }
                .btn {
                    border-color: #8D4F2D;
                    box-shadow: 5px 7px 0 #8D4F2D;
                    background: #BC7751;
                    &:hover {
                        color: #fff;
                        background: #8D4F2D;
                    }
                }
            }
        }
    }
    .single-product-section {
        .slideshow2-nav .thumb-img {
            border: 2px solid #8D4F2D;
            box-shadow: 5px 7px 0 #8D4F2D;
        }
    }
    .ingredients {
        h3 {
            color: #BC7751;
        }
        .column {
            p {
                color: #BC7751;
            }
        }
    }
    .nutritional-info {
        h3 {
            color: #BC7751;
        }
    }
    .custom-accordion {
        .custom-card {
            .custom-accordion-header {
                color: #BC7751;
                border-color: #BC7751;
                &:after {
                    background: url('../images/chevron-down-peach.svg');
                }
            }
        }
        .list3 li {
            border-color: #BC7751;
            span {
                color: #BC7751;
            }
        }
    }
}

