a, .btn {
    @include cubic-transition;
}

.btn {
    border-width: 2px;
    border-radius: 10px;
    font-size: rem-calc(20);
    font-weight: 700;
    padding: 15px 15px;
    &:focus { 
        outline: 0px; box-shadow: none; 
    }
}

@media(max-width:767px) {
    .btn {
        padding: 11px 15px;
        font-size: rem-calc(18);
        line-height: 28px;
    }
}

.rounded-btn {
    border-radius: 50px !important;
}

.btn-primary {
    border-color: $primary-dark;
    box-shadow: 5px 7px 0 $primary-dark;
    &.bg-color1 {
        color: $primary;
        &:hover {
            color: $primary;
        }
    }
    &:hover {
        color: #F7E8F3;
        background: $primary-dark;  
    }
}

.btn-outline-primary {
    box-shadow: 5px 7px 0 $primary;
    &.bg-color1 {
       &:hover {
        background: $primary !important;
        color: $white;
       }
    }
}

.btn-primary-light {
    color: $white;
    border-color: $primary;
    box-shadow: 5px 7px 0 $primary;
    &:hover {
        color: $white;
        border-color: $primary;
        background: $primary;
    }
}

.btn1 {
    background: #D5B7D9;
    color: $primary;
    border-color: $primary;
    box-shadow: 5px 7px 0 $primary;
    &:hover {
        background: $primary;
        border-color: $primary;
    }
}

.btn2 {

}

.btn-xs {
    font-size: rem-calc(13);
    padding: 5px 14px;
}

.btn-sm {
    font-size: 18px;
    line-height: 28px;
    padding: 11px 15px;
}

.btn-xl {
    font-size: rem-calc(24);
    padding: 15px 22px;
}

.toggle-btn {
    font-size: rem-calc(22);
    display: inline-flex;
    align-items: center;
    line-height: 1;
    span {
        border-bottom: 1px solid $primary;
        color: $primary;
        @include cubic-transition;
    }
    .icon {
        margin-left: 9px;
        margin-top: -9px;
        @include cubic-transition;
    }
}

.read-more-btn {
    text-transform: uppercase;
    display: inline-block;
    font-size: rem-calc(20);
    line-height: 1;
    font-weight: 700;
    color: $primary;
    border-bottom: 1px solid $primary;
}

@media(max-width:767px) {
    .read-more-btn {
        font-size: rem-calc(16);
    }
    .btn-xl {
        font-size: rem-calc(20);
        padding: 14px 17px;
    }
}

.download-options {
    margin-bottom: 24px;
    .btn {
        font-weight: 300;
        display: inline-flex;
        align-items: center;
        margin-right: 10px;
        margin-bottom: 10px;
        &:last-child {
            margin-right: 0;
        }
        .icon {
            margin-right: 10px;
        }
    }
}