/*
*section
*/
.section {
    padding: 50px 0;
    &.relative {
      .container {
        position: relative;
        z-index: 10;
      }
    }
    .cloud1 {
      top: 50%;
      //left: 48px;
      animation-delay: -160.69230769s;
      animation-duration: 88s;  
      background-size: 137px 84px;
    }
    .cloud2 {
        bottom: 50px;
        height: 60px;
        //left: 102px;
        animation-delay: -158.69230769s;
        animation-duration: 88s;
        background-size: 99px 60px;
    }
    .cloud3 {
        top: 124px;
        height: 113px;
        animation-delay: -18.46153846s;
        animation-duration: 210s;
        background-size: 186px 113px;
    }
    .cloud4 {
       top: 50%;
        height: 60px;
        //right: 214px;
        animation-delay: -22.46153846s;
        animation-duration: 200s;
        background-size: 99px 60px;
    }
    overflow-x: clip;
    header {
      &.c-mb-4 {
        margin-bottom: 40px !important;
      }
    }
    &.after-gradient {
      margin-bottom: 249px;
      position: relative;
      &:after {
        content: '';
        width: 100%;
        height: 249px;
        position: absolute;
        bottom: -249px;
        left: 0;
        background: rgb(162,114,175);
        background: linear-gradient(180deg, rgba(162,114,175,1) 0%, rgba(247,232,243,1) 100%);
      }
    }
}

.column {
    margin-bottom: 30px;
}

@media (max-width: 991px) {
  .section {
    &.after-gradient {
        margin-bottom: 150px;
        &:after {
          height: 150px;
          bottom: -150px;
        }
    }
  }
}

@media (max-width: 767px) {
  .section {
    &.after-gradient {
        margin-bottom: 50px;
        &:after {
          height: 50px;
          bottom: -50px;
        }
    }
    header {
      &.c-mb-4 {
        margin-bottom: 20px !important;
      }
    }
  }
}


