/*
*sign up
*/

.sign-up-section {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 47.78px;
        background: url('../images/sign-up-before.svg');
        background-repeat: repeat-x;
        background-size: 727px 67.48px;
        top: -42px;
        left: 0;
        animation: wave 200s cubic-bezier( 0.36, 0.36, 0.36, 0.36) infinite;
    }
    &.alt {
      &:before {
        background: url('../images/sign-up-before2.svg');
        background-size: 727px 67.48px;
      }
      .container {
        .row {
          position: relative;
          z-index: 10;
        }
        &:before {
          content: '';
          width: 254px;
          height: 250px;
          background: url('../images/sweet-chocolate-cookies2.png') no-repeat;
          position: absolute;
          left: -5%;
          bottom: 1px;
        }
        &:after {
          content: '';
          width: 254px;
          height: 250px;
          background: url('../images/sweet-chocolate-cookies2.png') no-repeat;
          position: absolute;
          right: -5%;
          top: -18px;
        }
      }
    }
}

@media (max-width: 991px) {
  .sign-up-section {
    &.alt {
      .container {
        &:before, &:after {
          width: 150px;
          height: 150px;
          background-size: contain;
        }
      }
    }
  }
}


@media (max-width: 991px) {
  .sign-up-section {
    &.alt {
      .container {
        &:before {
          left: -100px;
        }
        &:after {
          right: -100px;
        }
      }
    }
  }
}

