  .figure1 {
    margin-bottom: 21.5px;
    position: relative;
    .video-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 1;
    }
    .hover {
      position: absolute;
      width: 100%;
      bottom: 44px;
      left: 0;
      z-index: 10;
      @include cubic-transition;
      opacity: 0;
      visibility: hidden;
    }
    img {
      border-radius: 10px;
    }
    
    video {
      object-fit: cover;
      border-radius: 10px;
    }
    .figure, img, video {
      height: 349px;
    }
  }
  &:hover {
    .figure1 {
      .video-overlay {
        opacity: 0;
        visibility: hidden;
      }
      .hover {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  .content {
    h4 {
      font-size: rem-calc(28);
      margin-bottom: 4px;
    }
    p {
      font-size: rem-calc(20);
      font-weight: 300;
    }
  }
}

